import style from "./FlowText.module.scss";
import clsx from "clsx";
import {useGSAP} from "@gsap/react";
import gsap from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";
import {useRef} from "react";
import SwapVertIcon from '@mui/icons-material/SwapVert';

gsap.registerPlugin(ScrollTrigger);

const breakPoint = 1240;

export const FlowText = () => {

    const appRef = useRef<HTMLDivElement>(null!);

    useGSAP((self) => {

        // @ts-ignore
        const textsRight = self.selector(".text-right") as TweenTarget[];
        // @ts-ignore
        const textsLeft = self.selector(".text-left") as TweenTarget[];

        const mm = gsap.matchMedia();

        mm.add({
            isDesktop: `(min-width: ${breakPoint}px)`,
            isMobile: `(max-width: ${breakPoint - 1}px)`,
            reduceMotion: "(prefers-reduced-motion: reduce)",
        }, (context) => {
            // @ts-ignore
            const isDesktop = context.conditions.isDesktop;
            //console.log(context.conditions)


            textsRight.forEach((item, index) => {
                gsap.timeline({
                    scrollTrigger: {
                        trigger: item,
                        start: `center center+=${isDesktop ? 200 : 100}px`,
                        end: `center center-=${isDesktop ? 200 : 100}px`,
                        //markers: true,
                        scrub: true,
                    }
                })
                    .to(item, {x: isDesktop ? 100 : 50})
                    .to(item, {x: 0})
            })

            textsLeft.forEach((item, index) => {
                gsap.timeline({
                    scrollTrigger: {
                        trigger: item,
                        start: `center center+=${isDesktop ? 200 : 100}px`,
                        end: `center center-=${isDesktop ? 200 : 100}px`,
                        //markers: true,
                        scrub: true,
                    }
                })
                    .to(item, {x: isDesktop ? -100 : -50})
                    .to(item, {x: 0})
            })

        }, appRef)

    }, {scope: appRef})

    return (
        <div className={style.flowText} ref={appRef}>

            <SwapVertIcon className={style.fixedLetter}
            />


            <div className={style.texts}>

                {
                    [0, 1].map(key => (
                        <div key={key}
                             className={style.part}
                        >
                            {
                                Array
                                    .from({length: 20}, (v, k) => k)
                                    .map(index => (
                                        <p key={index}
                                           className={clsx(style.text, key === 0 ? "text-left" : "text-right")}
                                        >
                                            Example
                                        </p>
                                    ))
                            }
                        </div>
                    ))
                }


            </div>
        </div>
    )
}