import React, {useLayoutEffect, useRef} from "react";
import style from "./Roadmap.module.scss";
import gsap from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";
import clsx from "clsx";
import {svgIcons} from "../../assets/svgIcons";
import {Before} from "../x_common/Before/Before";
import {After} from "../x_common/After/After";

gsap.registerPlugin(ScrollTrigger);

export const Roadmap = () => {
    const appRef = useRef<HTMLDivElement>(null!);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            const iconWrappers = gsap.utils.toArray<gsap.DOMTarget>(".iconWrapper");
            const icons = gsap.utils.toArray<gsap.DOMTarget>(".wrapper svg");
            const steps = gsap.utils.toArray<gsap.DOMTarget>(".step");
            const lines = gsap.utils.toArray<gsap.DOMTarget>(".line");
            const labels = gsap.utils.toArray<gsap.DOMTarget>(".label");

            iconWrappers.forEach(iconWrapper => {
                gsap.to(iconWrapper, {
                        scale: 2,
                        borderColor: "lime",
                        boxShadow: "0 0 10px 0px lime",
                        duration: 0.3,
                        scrollTrigger: {
                            trigger: iconWrapper,
                            //markers: true,
                            start: "center center",
                            end: "center center",
                            toggleActions: "play none reverse none",
                        }
                    }
                )
            })

            icons.forEach(icon => {
                gsap.to(icon, {
                        scale: 2,
                        duration: 0.3,
                        scrollTrigger: {
                            trigger: icon,
                            //markers: true,
                            start: "center center",
                            end: "center center",
                            toggleActions: "play none reverse none",
                        }
                    }
                )
            })

            steps.forEach(step => {
                gsap.to(step, {
                        scale: 0,
                        duration: 0.1,
                        scrollTrigger: {
                            trigger: step,
                            //markers: true,
                            start: "center center",
                            end: "center center",
                            toggleActions: "play none reverse none",
                        }
                    }
                )
            })

            lines.forEach(line => {
                gsap.to(line, {
                        scaleY: 1,
                        ease: "none",
                        scrollTrigger: {
                            trigger: line,
                            //markers: true,
                            start: "top center",
                            end: "top+=400px center",
                            scrub: true,
                        }
                    }
                )
            })

            gsap.to(".light", {
                y: 2000,
                ease: "none",
                scrollTrigger: {
                    trigger: ".items",
                    //markers: true,
                    start: "top center",
                    end: "bottom center",
                    scrub: true,
                }
            })

            labels.forEach(label => {
                gsap.to(label, {
                        opacity: 1,
                        left: "80px",
                        duration: 0.5,
                        scrollTrigger: {
                            trigger: label,
                            //markers: true,
                            start: "center bottom",
                            end: "center bottom",
                            toggleActions: "play none reverse none",
                        }
                    }
                )
            })

            labels.forEach(label => {
                gsap.to(label, {
                        color: "lime",
                        duration: 0.5,
                        scrollTrigger: {
                            trigger: label,
                            //markers: true,
                            start: "center center",
                            end: "center center",
                            //scrub: true,
                            toggleActions: "play none reverse none",
                        }
                    }
                )
            })


        }, appRef)
        return () => ctx.revert();
    }, [])

    return (
        <div className={style.roadmap}
             ref={appRef}
        >

            <div className={style.beforeWrapper}>
                <Before className={style.before}
                        contentColor="rgba(255, 255, 255, 0.1)"
                />
            </div>

            <div className={clsx(style.items, "items")}>
                <div className={clsx(style.light, "light")}/>

                {
                    [0, 1, 2, 3, 4, 5].map(index => (
                        <div className={style.item}
                             key={index}
                        >
                            <div className={clsx(style.wrapper, "wrapper")}>
                                <div className={clsx(style.iconWrapper, "iconWrapper")}/>
                                {svgIcons.check}
                                <p className={clsx(style.step, "step")}>{index + 1}</p>
                                <p className={clsx(style.label, "label")}>Step {index + 1}</p>
                            </div>
                            {
                                index !== 5 && (
                                    <div className={style.lineWrapper}>
                                        <div className={clsx(style.line, "line")}/>
                                    </div>
                                )
                            }

                        </div>

                    ))
                }
            </div>

            <div className={style.afterWrapper}>
                <After className={style.after}
                       contentColor="rgba(255, 255, 255, 0.05)"
                />
            </div>




        </div>
    )
}
