import style from "./ParallaxImages.module.scss";
import src1 from "../../assets/jpg/image1.jpg";
import src2 from "../../assets/jpg/image2.jpg";
import src3 from "../../assets/jpg/image3.jpg";
import src4 from "../../assets/jpg/image4.jpg";
import src5 from "../../assets/jpg/image5.jpg";
import src6 from "../../assets/jpg/image6.jpg";
import src7 from "../../assets/jpg/image7.jpg";
import src8 from "../../assets/jpg/image8.jpg";
import {useRef} from "react";
import {useGSAP} from "@gsap/react";
import gsap from "gsap";
import clsx from "clsx";
import {Before} from "../x_common/Before/Before";
import {After} from "../x_common/After/After";

const images = [
    src1, src2, src3, src4, src5, src6, src7, src8
]

export const ParallaxImages = () => {

    const appRef = useRef<HTMLDivElement>(null!);

    useGSAP(() => {

        for (let key = 0; key < images.length; key++) {
            gsap.to(`.image${key}`, {
                    top: 0,
                    ease: "none",
                    scrollTrigger: {
                        trigger: `.item${key}`,
                        start: "top bottom",
                        end: "bottom top",
                        scrub: true,
                        //markers: true,
                    }
                }
            )
        }

    }, {scope: appRef})

    return (
        <div className={style.parallaxImages}
             ref={appRef}
        >
            <div className={style.inner}>

                <div className={style.beforeWrapper}>
                    <Before className={style.before}/>
                </div>

                <div className={style.items}>
                    {
                        images.map((src, key) => (
                            <div key={key}
                                 className={clsx(style.item, `item${key}`)}
                            >
                                <img src={src}
                                     alt=""
                                     className={`image${key}`}
                                />
                            </div>
                        ))
                    }
                </div>

                <div className={style.wrapper}>
                    <After className={style.before}/>
                </div>

            </div>


        </div>
    )
}