import React, {useLayoutEffect, useRef} from "react";
import style from "./Timeline.module.scss";
import gsap from "gsap";
import {CustomEase} from "gsap/CustomEase";
import TweenTarget = gsap.TweenTarget;

gsap.registerPlugin(CustomEase);

export const Timeline = () => {
    const appRef = useRef<HTMLDivElement>(null!);
    const count = 8;
    const duration = 1;

    // CustomEase.create("custom", "M0,0 C0.292,0.01 0.419,0.212 0.496,0.348 0.584,0.504 0.754,0.842 0.8,1 0.808,0.985 0.814,0.92 0.848,0.858 0.896,0.769 0.922,0.8 0.948,0.848 0.971,0.89 0.978,0.91 1,1 ");
    // CustomEase.create("custom", "M0,0,C0.274,0.18,0.374,0.289,0.476,0.408,0.586,0.536,0.754,0.842,0.8,1,0.808,0.985,0.814,0.92,0.848,0.858,0.896,0.769,0.922,0.8,0.948,0.848,0.971,0.89,0.978,0.91,1,1");
    //CustomEase.create("custom", "M0,0 C0.222,0 0.415,0.112 0.5,0.212 0.6,0.33 0.736,0.682 0.8,1 0.812,0.964 0.8,0.97 0.816,0.938 0.838,0.892 0.871,0.898 0.91,0.922 0.942,0.942 0.958,0.952 1,1 ");
    CustomEase.create("custom", "M0,0,C0.222,0,0.415,0.112,0.5,0.212,0.6,0.33,0.736,0.682,0.8,1,0.812,0.964,0.814,0.922,0.826,0.892,0.844,0.845,0.87,0.832,0.904,0.868,0.933,0.898,0.954,0.906,1,1");

    const ease = "custom"; // "bounce.out";
    const yoyoEase = "custom"; // "bounce.out";

    useLayoutEffect(() => {
        const ctx = gsap.context((self) => {

            const tl = gsap.timeline({repeat: -1, yoyo: true});

            // @ts-ignore
            const boxes = self.selector(".box") as TweenTarget[];

            boxes.forEach((box, index) => {
                tl.to(box, {
                    x: (window.innerWidth - 80) / count - 100,
                    duration, ease, yoyoEase
                }, index === 0 ? ">" : ">-0.2")
            })

        }, appRef);
        return () => ctx.revert();
    }, []);

    return (
        <div className={style.timeline}>
            <div className={style.firstRow} ref={appRef}>
                {
                    Array
                        .from({length: count}, (_, k) => k)
                        .map((_, key) => (
                            <div key={key}
                                 className="box"
                            />
                        ))
                }
            </div>

        </div>
    )
}
