import React, {useLayoutEffect, useRef} from "react";
import style from "./Control.module.scss"
import gsap from "gsap";
import clsx from "clsx";
import {Button} from "@mui/material";

export const Control = () => {
    const appRef = useRef<HTMLDivElement>(null!);
    const tweenRef = useRef<gsap.core.Tween>(null!);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            tweenRef.current = gsap.to(".box", {
                    x: window.innerWidth - 180,
                    rotation: 360,
                    duration: 5,
                    repeat: -1,
                    yoyo: true,
                }
            );
        }, appRef);
        return () => ctx.revert();
    }, []);

    const onPlay = () => tweenRef.current.play();
    const onPause = () => tweenRef.current.pause();
    const onResume = () => tweenRef.current.resume();
    const onReverse = () => tweenRef.current.reverse();
    const onRestart = () => tweenRef.current.restart();

    return (
        <div className={style.control} ref={appRef}>
            <div className={style.buttons}>
                <Button onClick={onPlay} variant="contained">Play</Button>
                <Button onClick={onPause} variant="contained">Pause</Button>
                <Button onClick={onResume} variant="contained">Resume</Button>
                <Button onClick={onReverse} variant="contained">Reverse</Button>
                <Button onClick={onRestart} variant="contained">Restart</Button>
            </div>

            <div className={clsx("box", style.box)}/>
        </div>
    )
}
