import React, {useLayoutEffect, useRef} from "react";
import style from "./PiningPanels.module.scss";
import gsap from "gsap";
import clsx from "clsx";

export const PiningPanels = () => {
    const appRef = useRef<HTMLDivElement>(null!);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            // gsap.to(".block1", {
            //         scrollTrigger: {
            //             trigger: ".block1",
            //             markers: true,
            //             scrub: true,
            //             start: "top top",
            //             end: "bottom 50px",
            //             pin: ".block1",
            //             pinSpacing: false,
            //             id: "block1",
            //         }
            //     }
            // );
            gsap.to(".block2", {
                    scrollTrigger: {
                        trigger: ".block2",
                        markers: true,
                        scrub: true,
                        start: "top 250px",
                        end: "bottom 300px",
                        pin: ".block1",
                        pinSpacing: false,
                        id: "block2",
                    }
                }
            );
            gsap.to(".block3", {
                    scrollTrigger: {
                        trigger: ".block3",
                        markers: true,
                        scrub: true,
                        start: "top 300px",
                        end: "bottom 350px",
                        pin: ".block2",
                        pinSpacing: false,
                        id: "block3",
                    }
                }
            )
            gsap.to(".block3", {
                    scrollTrigger: {
                        trigger: ".block3",
                        markers: true,
                        scrub: true,
                        start: "top 300px",
                        end: "bottom 350px",
                        pin: ".block1",
                        pinSpacing: false,
                        id: "block3",
                    }
                }
            )

        }, appRef);
        return () => ctx.revert();
    }, []);

    return (
        <div className={style.piningPanels} ref={appRef}>

            <div className={style.top}></div>

            <div className={clsx(style.bloc1, "block1")}>block1</div>
            <div className={clsx(style.block2, "block2")}>block2</div>
            <div className={clsx(style.block3, "block3")}>block3</div>


            <div className={style.bottom}></div>
        </div>
    )
}
