import React, {useState} from "react";
import style from "./Home.module.scss";
import {productionRoutes, studyRoutes} from "../A1_Header/routes";
import {Link} from "react-router-dom";
import clsx from "clsx";

export const Home = () => {
    const [mode, setMode] = useState("Production")

    return (
        <div className={style.home}>
            <div className={style.inner}>

                <div className={style.contentMobile}>

                    <div className={clsx({
                        [style.switcher]: true,
                        [style.switcher_right]: mode === "Development",
                    })}>
                        {
                            [
                                "Production",
                                "Development"
                            ].map((m, key) => (
                                <button key={key}
                                        onClick={() => setMode(m)}
                                        className={clsx({
                                            [style.btn]: true,
                                            [style.btn_selected]: m === mode,
                                        })}
                                >
                                    <span>{m}</span>
                                </button>
                            ))
                        }
                    </div>

                    <div className={style.links}>
                        {
                            (mode === "Production" ? productionRoutes : studyRoutes)
                                .map(({label, to}, key) => (
                                    <Link key={key}
                                          to={to}
                                          className={style.link}
                                    >
                                        {label}
                                    </Link>
                                ))
                        }
                    </div>

                </div>


                <div className={style.contentDesktop}>
                    {
                        [
                            {label: "Production", routes: productionRoutes},
                            {label: "Development", routes: studyRoutes},
                        ].map(({label, routes}, key) => (
                            <div className={style.column} key={key}>
                                <h3 className={style.label}>{label}</h3>
                                <div className={style.links}>
                                    {
                                        routes.map(({label, to}, key) => (
                                            <Link key={key}
                                                  to={to}
                                                  className={style.link}
                                            >
                                                {label}
                                            </Link>
                                        ))
                                    }
                                </div>

                            </div>
                        ))
                    }
                </div>

            </div>
        </div>
    )
}


