import style from "./StepByStep.module.scss";
import {Before} from "../x_common/Before/Before";
import gsap from "gsap";
import {useGSAP} from "@gsap/react";
import {ScrollTrigger} from "gsap/ScrollTrigger";
import {useRef} from "react";
import clsx from "clsx";
import {After} from "../x_common/After/After";

gsap.registerPlugin(ScrollTrigger);

export const StepByStep = () => {
    const appRef = useRef<HTMLDivElement>(null!);

    useGSAP(() => {

        gsap.to(".steps", {
                scrollTrigger: {
                    trigger: ".steps",
                    start: "top bottom",
                    end: "bottom bottom",
                    pin: ".back",
                    pinSpacing: false,
                    //markers: true,
                }
            }
        )

    }, {scope: appRef})

    return (
        <div className={style.stepByStep}
             ref={appRef}
        >

            <div className={style.inner}>

                <div className={style.beforeWrapper}>
                    <Before className={style.before}/>
                </div>

                <div className={style.wrapper}>
                    <div className={clsx(style.back, "back")}>
                        <p>pinning</p>
                        <p>background</p>
                    </div>
                </div>

                <div className={clsx(style.steps, "steps")}>
                    {
                        [0, 1, 2].map(el => (
                            <div key={el}
                                 className={style.step}
                            >
                                <p>step {el + 1}</p>
                            </div>
                        ))
                    }
                </div>

                <div className={style.wrapper}>
                    <After className={style.before}/>
                </div>


            </div>


        </div>
    )
}