import gsap from "gsap";

// Многие вспомогательные утилиты опционально возвращают функцию, которую можно использовать в анимациях, там где
// возможно использование функциональной логики. В этом случае утилиты будут вызываться один раз для каждой цели,
// а не использовать одно и тоже конечное значения всегда

//========= CHECK PREFIX =========//
// Принимает имя CSS свойства, возвращает имя с префиксом для текущего браузера
// Если префикс не требуется - возвращает входное значение, если входного CSS свойства не существует - undefined
export const getPrefixName = (cssName: string) => gsap.utils.checkPrefix(cssName);


//========= CLAMP =========//
// Ограничение значения в определенном интервале между минимумом и максимумом:
// 1. Если входной параметр меньше минимума - возращается минмум
// 2. Если входной параметр больше максимума - возращается максимум
// 3. Если входной параметр между минимумом и максимумом - возращается без изменений

// Существует две сигнатуры использование - немедленно получить значение или получить переиспользуемую функцию:
// 1. Немедленно получить значение:
// clamp(minimum, maximum, valueToClamp)
// [minimum, maximum] - промежуток ограничивающий входное знасение valueToClamp
// 2. получить переиспользуемую функцию, которая принимает параметр [valueToClamp] для заданного интервала [minimum, maximum]
// clamp(minimum, maximum)

export const clampValue = () => {
    console.log("clamp(50, 100, 25): ", gsap.utils.clamp(50, 100, 25));
    console.log("clamp(50, 100, 125): ", gsap.utils.clamp(50, 100, 125));
    console.log("clamp(50, 100, 75): ", gsap.utils.clamp(50, 100, 75));
}
export const clampFunction = gsap.utils.clamp(50, 100);

//========= DISTRIBUTE =========//
//

//========= GET UNIT =========//
// Возвращает еденицу измерения входной строки CSS значения, где на первом месте - число, затем - еденица измерения
// getUnit(s: string): string
export const getUnit = (cssProp: string) => gsap.utils.getUnit(cssProp);

//========= INTERPOLATE =========//
// Возвращает значение линейной интерполяции между двумя значениями подобного типа (число, цвет, строка, массив, сложная
// строка с несколькими наборами входящих в нее чисел, объект с несколькими свойствами)
// Сигнатуры использования:
// 1. Сразу получить интерполированное значение на основе границ  интерполяции и точки прогреса
// interpolate(startValue, endValue, progress);
// startValue и endValue - начальное и конечное значения интерполяции подобного типа (число, цвет, строка, массив, сложная строка, объект)
// progress - число 0...1 - пропорциональное положение точки на интервале интерполяции где берется значение
// 2. Сразу получить интерпалированное значение на основе массива точек диапазона интерполяции и точки прогресса
// interpolate(array, progress)
// gsap.utils.interpolate([10, 50, 80], 0.25)
// 3. Получить функцию интерполяции для заданого диапазона, принимающую точку прогресса в качестве параметра
// interpolate(startValue: T, endValue: T): func: (progress: number) => T
export const interpolate = gsap.utils.interpolate("100px", "200px");
// 4. Получить функцию интерполяции для заданого массива точек из диапазоно интерполяции, принимающую точку прогресса в качестве параметра
// interpolate(array: T[]): func: (progress: number) => T
export const interpolateArray = gsap.utils.interpolate([50, 100, 200]);

//========= MAP RANGE =========//
// Возвращает число из выходного диапазона, относительное положение которого в нем соответствует положению заданного числа
// во входном диапазоне
// пример применения - соответствие между перемещением ползунка слайдера и положение объекта на экране
// 1. mapRange(inMin: number, inMax: number, outMin: number, outMax: number, valueToMap: number): number
// сразу возвращает значение
// inMin, inMax - минимум и максимум входного диапазона
// outMin, outMax - минимум и максимум выходного диапазона
// valueToMap - значение из выходного диапазона которое должно быть отображено в значение из выходного диапазона
// 2. mapRange(inMin, inMax, outMin, outMax): (valueToMap: number) => number
// Возвращает переиспользуемую функцию которая принимает параметр valueToMap
export const mapRange = gsap.utils.mapRange(0, 100, 200, 800);

//========= NORMALIZE =========//
// Преобразует значение из заданного диапазона в соответствующее значение из диапазона [0, 1]
// Аналогична функции mapRange у которой outMin = 0, outMax = 1
// 1. normalize(min: number, max: number, valueToNormalize): number
// Возвращает нормализованное значение
// 2. normalize(min, max): (valueToNormalize) => number
// Возвращает функцию которая принимает значение для нормализации
export const normalize = gsap.utils.normalize(0, 10);

//========= PIPE =========//
// Связывает вызов нескольких функций когда результат выполнения одной передается в качестве входного параметра
// послудующей функции

// without pipe()
// var value1 = func1(input);
// var value2 = func2(value1);
// var output = func3(value2);

// or multi-level wrapping (awkward)
// var output = func1(func2(func3(input)));

// cleaner with pipe()
// var transfrom = gsap.utils.pipe(func1, func2, func3);
// var output = transform(input);

//========= RANDOM =========//
// Получить случайное число в указанном диапазоне (возможно округление до ближайшего зачения с шагом) или случайное число из предоставленного массива
// 1. random(min, max[, snapIncrement, returnFunction]): number | () => number
// min: number, max: number - диапазон [min, max]
// snapIncrement?: number - шаг точек округления (округляет до ближайшего min + n * snapIncrement)
// returnFunction?: boolean - если true возвращается не случайное значение, а функция, которая возвращает случайное значение
export const random = gsap.utils.random(0, 10, 0, true)
// 2. random(array[, returnFunction]): number | () => any
// array: any[] - массив сущностей из которых выбирается случайная
// returnFunction?: boolean - если true возвращается не случайное значение, а функция, которая возвращает случайное значение
export const randomFromArray = gsap.utils.random([0, 1, 4, 7, 9], true);
// 3. random(min, max[, returnFunction])
// Аналогично п. 1 но без параметра snapIncrement

//========= SELECTOR =========//
// Возвращает функцию-селектор (которая возвращает набор компонентов по селектору), область действия которой ограничена
// указанным компонентом. Аналогично querySelectorAll применительно к указанному компоненту, а не ко всему компоненту
// --- Функция-селектор возвращает массив, а не NodeList
// --- В качестве параметра может принимать react ref

// const selector = gsap.utils.selector(".app")
// const boxes = selector(".box);
// gsap.to(selector(".circle"), { x: 100 })

// const ref = useRef<HTMLDivElement>(null!);
// const sel = gsap.utils.selector(ref);
// const boxes = sel(".box);

//========= SHUFFLE =========//
// В качестве входного параметра принимает массив, случайным образом меняет местами его члены (перемешивает) и
// возвращает тот же массив

//========= SNAP =========//
// Привязка (округление) к значению с определенным шагом или ближайшему элементу из массива
// Возможно ограничение привязки в пределах диапазона (одномерное) или круга (двумерное)

// 1. snap(snapIncrement, valueToSnap): number
// Возвращает округленное до ближайшего с шагом
// snapIncrement: number - шаг привязки
// valueToSnap: number - округляемое число

// 2. snap(snapIncrement): (valueToSnap: number) => number
// Возвращает функцию которая принимает значение для округления и возвращает округленное до шага значение
export const snap = gsap.utils.snap(2);

// 3. snap(array, valueToSnap): number
// Возвращает ближайшее целое из массива

// 4. snap(array): (valueToSnap: number) => number
// Возвращает функцию которая принимает значение для округления и возвращает округленное до элемента массива значение
export const snapToArray = gsap.utils.snap([1, 3, 9]);

// 5. snap(objectWithRadius, valueToSnap): number | {x: number, y: number}
// objectWithRadius: object
// objectWithRadius: {
//     radius: number
//     values: number[] | {x: number, y: number}[] или increment: number
// }
// valueToSnap: number | {x: number, y: number}
// Возвращает ближайшее значение из values в пределах radius от него или входное значение

// 6. snap(objectWithRadius): (valueToSnap: number | {x: number, y: number}) => number | {x: number, y: number}
// objectWithRadius: object
// objectWithRadius: {
//     radius: number
//     values: number[] | {x: number, y: number}[] или increment: number
// }
// valueToSnap: number
// Возвращает функцию которая возвращает ближайшее значение из values в пределах radius от него или входное значение
export const snapWithRadius = gsap.utils.snap({values: [10, 50], radius: 10});

//========= SPLIT COLOR =========//
// splitColor(color: string, returnHSL?: boolean)
// Преобразует строковое обозначение цвета ("rgb()", "rgba()", "hsl()", "hsla()", "red", "blue")
// в массив вида [red: number, green: number, blue: number, alpha?: number] или
// [hue: number, saturation: number, lightness: number, alpha?: number] если returnHSL: true

//========= TO ARRAY =========//
// toArray(target, scope)
// target: string, string[], object[], NodeList - строковый селектор, массив объектов или строковых селектров,
// объект, список нод или любой массиво подобный объект
// scope?: element | ref - ограничивает область применения (возвращает только потомков элемента)

//========= UNITIZE =========//
// unitize(function, unit): (p: string | number): string
// function - функция которая возвращает число
// unit?: string - строковое представление единицы измерения
// Возвращает функцию которая к результату функции function добавляет строку unit
// Возвращаемая функция может принимать строку в виде конкатенации цисла и строки-единицы измерения
// Если задан параметр unit, то входной параметр возвращаемой функции в случае если он вида число+строка_едининицы_измерения
// будет отделен от едининицы измерения, а к результату возвращаемой функции будет прибавлен unit
// Если параметр unit не задан, то еденица измерения входного параметра останется неизменной
export const unitizeClamp = gsap.utils.unitize(gsap.utils.clamp(10, 20), "vw");

//========= WRAP =========//
// wrap(min: number, max: number, index?: number)
// возвращает элемент из диапазона [min, max) (max не входит) с индексом [index] (отсчет с 0 и идет по кругу)
// или если [index] не задан - функцию, которая возвращает такой элемент по индексу
export const wrapRange = gsap.utils.wrap(5, 10);
// 0 - 5   2 - 7   4 - 9
// 1 - 6   3 - 8   5 - 5

// wrap(array: any[], index?: number)
// возвращает элемент из массива с индексом [index] (причем отсчет индекса начинается с 0
// и идет по кругу) или если [index] не задан - функцию, которая принимает [index] и возвращает слудующий элемент.
export const wrapArray = gsap.utils.wrap(["one", "two", "three", "four"]);

//========= WRAP YOYO =========//
// Аналогичен wrap, но отсчет начинается с последнего элемента и идет справо налево (диапазон [min, max])
// и цикличность не по кругу, а из стороны в сторону
export const wrapYoyoRange = gsap.utils.wrapYoyo(5, 10);
// 0 - 10   2 - 8   4 - 6   6 - 6
// 1 - 9    3 - 7   5 - 5   7 - 7
// отсчет с первого элемента и цикличность не по кругу, а из стороны в сторону
export const wrapYoyoArray = gsap.utils.wrapYoyo(["one", "two", "three", "four"]);
// 0 - one   2 - three   4 - three
// 1 - two   3 - four    5 - two

