import React, {useLayoutEffect, useRef} from "react";
import style from "./AnchorNavigation.module.scss"
import clsx from "clsx";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import gsap from "gsap";

export const AnchorNavigation = () => {

    const onAnchorClickHandler = (e: React.MouseEvent<HTMLAnchorElement, MouseEvent>) => {
        e.preventDefault();
        // @ts-ignore
        const href = e.target.getAttribute("href");
        //console.log(href)
        const target = document.querySelector(href as string); // элемент к которому переходят
        const panelsContainer = document.querySelector(".panelsContainer");
        if (target && panelsContainer) {
            if (panelsContainer.isSameNode(target.parentElement)) {

            }
        }
    }

    const appRef = useRef<HTMLDivElement>(null!);
    const panelsRef = useRef<HTMLDivElement>(null!);

    useLayoutEffect(() => {
        const panels = gsap.utils.toArray(".panel");

        const ctx = gsap.context(() => {
           gsap.to(panels, {
               xPercent: -100 * (panels.length - 1),
               ease: "none",
               scrollTrigger: {
                   trigger: ".panelsContainer",
                   pin: true,
                   start: "top top",
                   markers: true,
                   scrub: true,
                   end: () => "+=" + panelsRef.current.offsetWidth,
                   snap: 1 / (panels.length - 1),
                   onSnapComplete: (self: ScrollTrigger) => console.log("onSnapComplete"),
               }
           })
        }, appRef);
        return () => ctx.revert();
    })

    return (
        <div className={style.anchorNavigation}
             ref={appRef}
        >
            <div className={style.navigations}>
                {
                    [
                        { href: "#intro", label: "Home" },
                        { href: "#panel-1", label: "Panel 1" },
                        { href: "#panel-3", label: "Panel 3" },
                        { href: "#panel-5", label: "Panel 5" },
                        { href: "#map", label: "Map" },
                    ].map(({label, href}, key) => (
                        <a href={href} key={key}
                           onClick={e => onAnchorClickHandler(e)}
                        >
                            {label}
                        </a>
                    ))
                }
            </div>

            <div className={style.intro} id="intro">
                <h1>Home Home Home Home Home</h1>
            </div>

            <div ref={panelsRef}>
                <div className={clsx(style.panelsContainer, "panelsContainer")}>

                    <div className={clsx(style.panel, style.panel1, "panel")}
                         id="panel1"
                    >
                        <h3>Panel 1 - Panel 1 - Panel 1</h3>
                        <div className={style.nav}>
                            {/*<a href="#" className={style.prev}>PREV</a>*/}
                            <a href="#panel-2" className={style.next}
                               onClick={e => onAnchorClickHandler(e)}
                            >
                                NEXT
                            </a>
                        </div>
                    </div>

                    <div className={clsx(style.panel, style.panel2, "panel")}
                         id="panel-2"
                    >
                        <h3>Panel 2 - Panel 2 - Panel 2</h3>
                        <div className={style.nav}>
                            <a href="#panel-1" className={style.prev}
                               onClick={e => onAnchorClickHandler(e)}
                            >
                                PREV
                            </a>
                            <a href="#panel-3" className={style.next}
                               onClick={e => onAnchorClickHandler(e)}
                            >
                                NEXT
                            </a>
                        </div>
                    </div>

                    <div className={clsx(style.panel, style.panel3, "panel")}
                         id="panel-3"
                    >
                        <h3>Panel 3 - Panel 3 - Panel 3</h3>
                        <div className={style.nav}>
                            <a href="#panel-2" className={style.prev}
                               onClick={e => onAnchorClickHandler(e)}
                            >
                                PREV
                            </a>
                            <a href="#panel-5" className={style.next}
                               onClick={e => onAnchorClickHandler(e)}
                            >
                                NEXT
                            </a>
                        </div>
                    </div>

                    <div className={clsx(style.panel, style.panel4, "panel")}
                         id="panel-4"
                    >
                        <h3>Panel 4 - Panel 4 - Panel 4</h3>
                        <div className={style.nav}>
                            <a href="#panel-3" className={style.prev}
                               onClick={e => onAnchorClickHandler(e)}
                            >
                                PREV
                            </a>
                            <a href="#panel-5" className={style.next}
                               onClick={e => onAnchorClickHandler(e)}
                            >
                                NEXT
                            </a>
                        </div>
                    </div>

                    <div className={clsx(style.panel, style.panel5, "panel")}
                         id="panel-5"
                    >
                        <h3>Panel 5 - Panel 5 - Panel 5</h3>
                        <div className={style.nav}>
                            <a href="#panel-4" className={style.prev}
                               onClick={e => onAnchorClickHandler(e)}
                            >
                                PREV
                            </a>
                        </div>
                    </div>
                </div>
            </div>

            <div className={style.map} id="map">
                <h3>Map Map Map Map Map</h3>
            </div>


        </div>
    )
}
