import React, {useLayoutEffect, useRef, useState} from "react";
import style from "./SkewOnScroll.module.scss";
import src1 from "../../assets/jpg/image1.jpg";
import src2 from "../../assets/jpg/image2.jpg";
import src3 from "../../assets/jpg/image3.jpg";
import src4 from "../../assets/jpg/image4.jpg";
import src5 from "../../assets/jpg/image5.jpg";
import src6 from "../../assets/jpg/image6.jpg";
import src7 from "../../assets/jpg/image7.jpg";
import src8 from "../../assets/jpg/image8.jpg";

import gsap from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

export const SkewOnScroll = () => {
    const appRef = useRef<HTMLDivElement>(null!);
    // Анимируется объект {skew: number}, где skew - угол наклона
    // Объект хранится в рефе (таким образом значение сохраняется между рендерами)
    const ref = useRef<{skew: number}>({skew: 0})

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            gsap.set(".skewImage", {
                transformOrigin: "right center",
                force3D: true
            })
            const proxy = { skew: 0 };
            // Улучшенная по производительности функция gsap.to (мгновенная установка свойства)
            const skewSetter = gsap.quickSetter(".skewImage", "skewY", "deg");
            // Ограничение угла наклона в промежутке [-20, 20]
            const clamp = gsap.utils.clamp(-5, 5);

            ScrollTrigger.create({
                trigger: appRef.current,
                onUpdate: (self) => {

                    // Линейная зависимость междду мгновенной скоростью прокрутки и углом наклона
                    const skew = clamp(Math.round(self.getVelocity() / -300));
                    //console.log(skew)

                    // обновляем анимацию если скорость прокрутки возросла
                    if (Math.abs(skew) > Math.abs(
                        //proxy.skew
                        ref.current.skew
                    )) {
                        // Мгновенно изменяем значение skew до более высокого
                        //proxy.skew = skew;
                        ref.current.skew = skew;

                        // Плавно анимирует убывание значения skew до 0
                        // При этом при изменении skew при каждом тике изменения мгновенно устанавливается угол наклона
                        // каждого элемента ".skewImage"
                        gsap.to(
                            //proxy,
                            // @ts-ignore
                            ref.current,
                            {
                            skew: 0,
                            duration: 2,
                            ease: "power3",
                            overwrite: true,
                            onUpdate: () => skewSetter(
                                //proxy.skew
                                ref.current.skew
                            )
                        });
                    }
                }
            })
        }, appRef)
        return () => ctx.revert();
    }, [])

    return (
        <div className={style.skewOnScroll}
             ref={appRef}
        >
            <div className={style.imgs}>
                {
                    [
                        src1, src2, src3, src4,
                        src5, src6, src7, src8,
                        src1, src2, src3, src4,
                        src5, src6, src7, src8
                    ].map((src, key)=> (
                        <img src={src} alt="" key={key} className="skewImage"/>
                    ))
                }
            </div>
        </div>
    )
}
