import style from "./After.module.scss"
import {FC} from "react";
import clsx from "clsx";

interface IAfter {
    className?: string
    contentColor?: string
}

export const After: FC<IAfter> = ({
                                      className,
                                      contentColor = "rgba(255,255,255, 0.5)",
                                  }) => {
    return (
        <div className={clsx(style.after, Boolean(className) && className)}>
            <p className={style.label}
               style={{
                   color: contentColor
               }}
            >
                After
            </p>
        </div>
    )
}