import React, {useLayoutEffect, useRef} from "react";
import style from "./StickyPanel.module.scss";
import {text} from "./const";
import clsx from "clsx";
import gsap from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";
import {Before} from "../x_common/Before/Before";
import {After} from "../x_common/After/After";

gsap.registerPlugin(ScrollTrigger);

export const StickyPanel = () => {
    const appRef = useRef<HTMLDivElement>(null!);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            gsap.to(".stickyPanel", {
                    //x: 500,
                    scrollTrigger: {
                        trigger: ".wrapper",
                        //markers: true,
                        end: "bottom 400px",
                        pin: ".panel",
                    }
                }
            )
        }, appRef);
        return () => ctx.revert();
    }, []);

    return (
        <div className={clsx(style.stickyPanel, "stickyPanel")}
             ref={appRef}
        >
            <Before className={style.blockBefore}/>

            <div className={clsx(style.wrapper, "wrapper")}>
                <div className={clsx(style.panel, "panel")}>
                    <p>Sticky Panel</p>
                    <p>Sticky Panel</p>
                    <p>Sticky Panel</p>
                    <p>Sticky Panel</p>
                    <p>Sticky Panel</p>
                    <p>Sticky Panel</p>
                    <p>Sticky Panel</p>
                    <p>Sticky Panel</p>
                    <p>Sticky Panel</p>
                    <p>Sticky Panel</p>
                </div>
                <div className={style.content}>
                    <p>{text}</p>
                    <p>{text}</p>
                </div>
            </div>

            <After className={style.after}/>

        </div>
    )
}
