import React, {useLayoutEffect, useRef, useState} from "react";
import style from "./GradientAnimation.module.scss";
import clsx from "clsx";
import gsap from "gsap";

export const GradientAnimation = () => {
    const appRef = useRef<HTMLDivElement>(null!);
    const [hover, setHover] = useState(false);
    const tweenRef = useRef<gsap.core.Tween>(null!);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {

            tweenRef.current = gsap.fromTo(".target", {
                    background: "linear-gradient(90deg, darkorange, blue)",
                },
                {
                    duration: 1,
                    background: "linear-gradient(90deg, green, red)",//hover ? "linear-gradient(90deg, yellow, blue)" : "linear-gradient(90deg, green, red)"
                }
            );
            tweenRef.current.pause();


        }, appRef);
        return () => ctx.revert();
    }, [hover])

    const onMouseEnter = () => tweenRef.current.play();
    const onMouseLeave = () => tweenRef.current.reverse();

    return (
        <div className={style.gradientAnimation}
             ref={appRef}
        >
            <div className={clsx(style.target, "target")}
                 onMouseEnter={onMouseEnter}
                 onMouseLeave={onMouseLeave}
            >
                <p>hover me</p>
            </div>


        </div>
    )
}
