import style from "./StandaloneScrollTrigger.module.scss";
import clsx from "clsx";
import React, {useLayoutEffect, useRef} from "react";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

export const StandaloneScrollTrigger = () => {
    const appRef = useRef<HTMLDivElement>(null!);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            const tween = gsap.to(".box2", {
                x: 500
            })
            ScrollTrigger.create({
                animation: tween,
                trigger: ".box1",
                endTrigger: ".box3",
                markers: true,
                start: "top bottom-=300px",
                end: "bottom 300px",
                scrub: 1,
        })
        }, appRef);

        return () => ctx.revert();
    }, [])

    return (
        <div className={style.standaloneScrollTrigger} ref={appRef}>
            <div className={clsx(style.box1, "box1")}>Start Scroll Trigger</div>
            <div className={clsx(style.box2, "box2")}>Animated by scroll</div>
            <div className={clsx(style.box3, "box3")}>End Scroll Trigger</div>

        </div>
    )
}
