import style from "./RunningString.module.scss";
import {DirectionEnum, RunningStringItem} from "../x_common/RunningStringItem/RunningStringItem";
import {useMediaQuery} from "@mui/material";
import KeyboardDoubleArrowRightIcon from '@mui/icons-material/KeyboardDoubleArrowRight';
import KeyboardDoubleArrowLeftIcon from '@mui/icons-material/KeyboardDoubleArrowLeft';
import KeyboardDoubleArrowUpIcon from '@mui/icons-material/KeyboardDoubleArrowUp';
import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';
import {useWindowSize} from "@uidotdev/usehooks";

export const RunningString = () => {
    const {width, height} = useWindowSize();

    const matchDesktop = useMediaQuery('(min-width:1240px)');


    const ROWS_COUNT = 7;
    const ROWS_DURATION = 120;

    const _row_height = matchDesktop
        ? ((height as number) - 40) / (2 * ROWS_COUNT + 1)
        : ((height as number) - 60) / (2 * ROWS_COUNT + 1)
    //const columnsCount = math.ceil()
    // (2 * n + 1) * _row_height = width
    const _columnCount = Math.round(((width as number) / _row_height - 1) / 2);
    const columnCount = _columnCount % 2 === 0 ? _columnCount + 1 : _columnCount;
    console.log("width: ", width)
    console.log("_row_height: ", _row_height)
    console.log(_columnCount, columnCount )


    const rowHeight = matchDesktop
        ? `calc((100vh - 60px) / (2 * ${ROWS_COUNT} + 1))`
        : `calc((100vh - 40px) / (2 * ${ROWS_COUNT} + 1))`;

    const rowTop = (key: number) => matchDesktop
        ? `calc((2 * ${key} + 1) * (100vh - 60px) / (2 * ${ROWS_COUNT} + 1))`
        : `calc((2 * ${key} + 1) * (100vh - 40px) / (2 * ${ROWS_COUNT} + 1))`;

    const column_gap = ((width as number) - columnCount * _row_height) / (columnCount + 1);
    const columnLeft = (key: number) => `calc(${key * _row_height + (key + 1) * column_gap}px)`

    return (
        <div className={style.runningString}>

                {
                    Array
                        .from({length: ROWS_COUNT}, (v, k) => k)
                        .map(key => (
                            <div key={key}
                                 className={style.row}
                                 style={{
                                     height: rowHeight,
                                     top: rowTop(key),
                                     zIndex: key % 2 === 0 ? 4 : 2
                                 }}
                            >
                                <RunningStringItem count={20}
                                                   forward={key % 2 === 0}
                                                   duration={ROWS_DURATION}
                                                   item={
                                                       <div className={style.rowItem}>
                                                            <p className={style.text}
                                                                style={{
                                                                fontSize: `${0.75 * 100 / (2 * ROWS_COUNT + 1)}vh`,
                                                                marginRight: `${0.5 * 100 / (2 * ROWS_COUNT + 1)}vh`,
                                                            }}
                                                            >
                                                                    {key % 2 === 0 ? "forward" : "backward"}
                                                            </p>
                                                           {
                                                               key % 2 === 0 ? (
                                                                   <KeyboardDoubleArrowRightIcon sx={{
                                                                       fontSize: `${0.75 * 100 / (2 * ROWS_COUNT + 1)}vh`,
                                                                       marginRight: `${0.5 * 100 / (2 * ROWS_COUNT + 1)}vh`,
                                                                       fontWeight: "bold"
                                                                   }}/>
                                                               ) : (
                                                                   <KeyboardDoubleArrowLeftIcon sx={{
                                                                       fontSize: `${0.75 * 100 / (2 * ROWS_COUNT + 1)}vh`,
                                                                       marginRight: `${0.5 * 100 / (2 * ROWS_COUNT + 1)}vh`,
                                                                   }}/>
                                                               )
                                                           }

                                                       </div>
                                                   }

                                />
                            </div>
                        ))
                }

            {
                Array
                    .from({length: columnCount}, (v, k) => k)
                    .map(key => (
                        <div key={key}
                             className={style.column}
                             style={{
                                 width: rowHeight,
                                 left: columnLeft(key),
                                 zIndex: key % 2 === 0 ? 3 : 1
                             }}
                        >
                            <RunningStringItem count={20}
                                               forward={key % 2 === 0}
                                               direction={DirectionEnum.vertical}
                                               duration={ROWS_DURATION}
                                               item={
                                                   <div className={style.columnItem}>
                                                       <p className={style.text}
                                                          style={{
                                                              fontSize: `${0.75 * 100 / (2 * ROWS_COUNT + 1)}vh`,
                                                              marginBottom: `${0.5 * 100 / (2 * ROWS_COUNT + 1)}vh`,
                                                          }}
                                                       >
                                                           {
                                                               (key % 2 === 0 ? "upward" : "downward").split("").map((letter, k) => (
                                                                   <span key={k}>
                                                                       {letter}
                                                                   </span>
                                                               ))
                                                           }
                                                       </p>
                                                       {
                                                           key % 2 === 0 ? (
                                                               <KeyboardDoubleArrowUpIcon sx={{
                                                                   fontSize: `${0.75 * 100 / (2 * ROWS_COUNT + 1)}vh`,
                                                                   marginBottom: `${0.5 * 100 / (2 * ROWS_COUNT + 1)}vh`,
                                                                   fontWeight: "bold"
                                                               }}/>
                                                           ) : (
                                                               <KeyboardDoubleArrowDownIcon sx={{
                                                                   fontSize: `${0.75 * 100 / (2 * ROWS_COUNT + 1)}vh`,
                                                                   marginBottom: `${0.5 * 100 / (2 * ROWS_COUNT + 1)}vh`,
                                                               }}/>
                                                           )
                                                       }

                                                   </div>
                                               }

                            />
                        </div>
                    ))
            }


        </div>
    )
}