import React from "react";
import style from "./Header.module.scss";
import {Link} from "react-router-dom";

export const Header = () => {
    return (
        <header className={style.header}>
            <div className={style.inner}>
                <Link to="/"
                      className={style.logo}
                >
                    Green Sock
                </Link>
            </div>
        </header>
    )
}
