import React, {FC, useLayoutEffect, useRef} from "react";
import style from "./WaveEnteringTitle.module.scss";
import gsap from "gsap";
import {ScrollTrigger} from "gsap/ScrollTrigger";
import clsx from "clsx";
import {CustomEase} from "gsap/CustomEase";

gsap.registerPlugin(ScrollTrigger, CustomEase);

export const WaveEnteringTitle = () => {
    const appRef = useRef<HTMLDivElement>(null!);

    CustomEase.create("custom", "M0,0,C0,0,0.05,0.228,0.09,0.373,0.12,0.484,0.139,0.547,0.18,0.654,0.211,0.737,0.226,0.806,0.254,0.89,0.27,0.938,0.274,0.967,0.29,1,0.306,1.034,0.333,1.09,0.348,1.118,0.374,1.166,0.392,1.233,0.414,1.272,0.444,1.326,0.488,1.404,0.53,1.416,0.57,1.427,0.608,1.426,0.642,1.398,0.672,1.372,0.736,1.222,0.76,1.19,0.815,1.115,0.822,1.035,0.91,1.011,0.943,1.002,1,1,1,1");


    useLayoutEffect(() => {
        const ctx = gsap.context(() => {

            gsap.from(".title1 .letter", {
                duration: 0.5,
                opacity: 0,
                stagger: 0.125
            })

            gsap.from(".title2 .letter", {
                duration: 0.5,
                opacity: 0,
                y: 100,
                stagger: 0.125,
                scrollTrigger: {
                    trigger: ".title2",
                    //markers: true,
                    start: "top bottom-=200px",
                    end: "top bottom-=200px",
                    toggleActions: "play none reverse none"
                }
            })

            gsap.from(".title3 .letter", {
                duration: 0.5,
                opacity: 0,
                y: 100,
                stagger: 0.025,
                scrollTrigger: {
                    trigger: ".title3",
                    //markers: true,
                    start: "top bottom-=200px",
                    end: "top bottom-=200px",
                    toggleActions: "play none reverse none"
                }
            })

            gsap.from(".title4 .letter", {
                duration: 0.75,
                scale: 0,
                stagger: 0.1,
                ease: "custom",
                scrollTrigger: {
                    trigger: ".title4",
                    //markers: true,
                    start: "top bottom-=200px",
                    end: "top bottom-=200px",
                    toggleActions: "play none reverse none"
                }
            })

            gsap.from(".title5 > .letter:nth-child(odd)", {
                duration: 0.4,
                opacity: 0,
                y: 100,
                stagger: 0.2,
                ease: "back.out",
                scrollTrigger: {
                    trigger: ".title5",
                    //markers: true,
                    start: "top bottom-=200px",
                    end: "top bottom-=200px",
                    toggleActions: "play none reverse none"
                }
            })

            gsap.from(".title5 > .letter:nth-child(even)", {
                duration: 0.4,
                opacity: 0,
                y: -100,
                stagger: 0.2,
                ease: "back.out",
                scrollTrigger: {
                    trigger: ".title5",
                    //markers: true,
                    start: "top bottom-=200px",
                    end: "top bottom-=200px",
                    toggleActions: "play none reverse none"
                }
            })

        }, appRef)
        return () => ctx.revert();
    }, [])

    return (
        <div className={style.waveEnteringTitle}
             ref={appRef}
        >
            <Title label="First title" target="title1"/>

            <p className={style.label}>scroll down</p>
            <div className={style.arrow}/>

            <Title label="Second title" target="title2" className={style.title_2}/>

            <Title label="Third title" target="title3" className={style.title_3}/>

            <Title label="Fourth title" target="title4" className={style.title_4}/>

            <Title label="Fifth title" target="title5" className={style.title_5}/>

        </div>
    )
}

//========= TITLE =========//
interface ITitle {
    target: string,
    label: string,
    className?: string
}
const Title: FC<ITitle> = ({target, label, className}) => {
    return (
        <h2 className={clsx(style.title, Boolean(className) && className, target)}>
            {
                label.split("").map((l, key) => (
                    <div key={key}
                         className={clsx({
                             "letter": l !== " ",
                             [style.empty]: l === " ",
                         })}
                    >
                        {l}
                    </div>
                ))
            }
        </h2>
    )
}
