import style from "./StepToStep.module.scss";
import {Before} from "../x_common/Before/Before";
import clsx from "clsx";
import {useRef} from "react";
import {useGSAP} from "@gsap/react";
import gsap from "gsap";
import {After} from "../x_common/After/After";
import {ScrollTrigger} from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

export const StepToStep = () => {

    const appRef = useRef<HTMLDivElement>(null!);

    useGSAP(() => {

        gsap.to(".steps", {
                scrollTrigger: {
                    trigger: ".steps",
                    start: "top bottom",
                    end: "bottom bottom",
                    pin: ".back",
                    pinSpacing: false,
                    //markers: true,
                }
            }
        )

        gsap.to(".step1", {
                scrollTrigger: {
                    trigger: ".step1",
                    start: "top bottom",
                    end: "top top",
                    pin: ".step0",
                    pinSpacing: false,
                    //markers: true,
                }
            }
        )

        gsap.to(".step2", {
                scrollTrigger: {
                    trigger: ".step2",
                    start: "top bottom",
                    end: "top top",
                    pin: ".step1",
                    pinSpacing: false,
                    //markers: true,
                }
            }
        )

        gsap.to(".step2", {
                scrollTrigger: {
                    trigger: ".step2",
                    start: "top bottom",
                    end: "top top",
                    pin: ".step0",
                    pinSpacing: false,
                    //markers: true,
                }
            }
        )

    }, {scope: appRef})



    return (
        <div className={style.stepToStep}
             ref={appRef}
        >
            <div className={style.inner}>

                <div className={style.beforeWrapper}>
                    <Before className={style.before}/>
                </div>

                <div className={clsx(style.wrapper, "back")}>
                    <div className={style.back}>
                        <p>pinning</p>
                        <p>background</p>
                    </div>
                </div>

                <div className={clsx(style.steps, "steps")}>
                    {
                        [0, 1, 2].map(key => (
                            <div key={key}
                                 className={clsx(style.step, `step${key}`)}
                                 style={{
                                     paddingTop: `${(key * 2 + 1) * 100 / 7}vh`
                                 }}
                            >
                                <p>step {key + 1}</p>
                            </div>
                        ))
                    }
                </div>

                <div className={style.wrapper}>
                    <After className={style.before}/>
                </div>

            </div>
        </div>
    )
}