import React, {FC, useRef} from "react";
import style from "./Before.module.scss";
import clsx from "clsx";
import gsap from "gsap";
import {useGSAP} from "@gsap/react";

interface IBefore {
    className?: string
    contentColor?: string
}

export const Before: FC<IBefore> = ({
                                        className,
                                        contentColor = "rgba(255,255,255, 0.5)"
                                    }) => {

    const appRef = useRef<HTMLDivElement>(null!);

    useGSAP(() => {

        gsap.to(".arrow", {
                yPercent: 50,
                yoyo: true,
                repeat: -1
            })

    }, {scope: appRef})

    return (
        <div className={clsx(style.before, Boolean(className) && className)}
             ref={appRef}
        >
            <p className={style.title}
               style={{color: contentColor}}
            >
                before
            </p>
            <p className={style.label}
               style={{color: contentColor}}
            >
                scroll down
            </p>
            <div className={clsx(style.arrow, "arrow")}
                 style={{
                     borderColor: contentColor
                 }}
            />
        </div>
    )
}