import gsap from "gsap";
import React, {useEffect, useLayoutEffect, useRef, useState} from "react";
import style from "./Basic.module.scss";
import clsx from "clsx";
import {
    clampFunction,
    clampValue,
    getPrefixName,
    getUnit,
    interpolate,
    interpolateArray,
    mapRange,
    normalize,
    random,
    randomFromArray,
    snap,
    snapToArray,
    snapWithRadius,
    unitizeClamp,
    wrapArray,
    wrapRange, wrapYoyoArray,
    wrapYoyoRange
} from "../../utils/utils";

export const Basic = () => {
    const appRef = useRef<HTMLDivElement>(null!);
    const circleRef = useRef<HTMLDivElement>(null!);
    const secondRef = useRef<HTMLDivElement>(null!);
    const thirdRef = useRef<HTMLDivElement>(null!);

    const [count, setCount] = useState(0);

    // useLayoutEffect is a version of useEffect that fires before the browser repaints the screen
    useLayoutEffect(() => {

        //========= DEFAULTS =========//
        // gsap.defaults({ duration: 2})
        // Позволяет установить свойства по-умолчанию, которые будут наследоваться всеми [tween] (если у них не установлено
        // inherit: false), пока они не будут переопределены в свойствах [tween]
        // Основные настройки конфигурации, не специфичные для [tween] (например unit, autoSleep) должны быть определены
        // с помощью gsap.config()

        // create our context. This function is invoked immediately and all GSAP animations and
        // ScrollTriggers created during the execution of this function get recorded so we can revert()
        // them later (cleanup)
        const ctx = gsap.context(() => {
                gsap.to(".blue", {
                    rotation: 360
                });
                gsap.to("#box", {
                    rotation: 360
                });
                gsap.to(circleRef.current, {
                    rotation: -360
                });
            },
            appRef // все ссылки на элементы в функции контекста будут искаться внутри элемента с указателем [appRef]
        )
        return () => ctx.revert(); // cleanup
    }, [])

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            // анимация от текущего состояния к указанному в объекте
            gsap.to(".to", {
                y: 100,
                opacity: 0.25,
                duration: 3,
            });

            // transform: rotate(360deg) translateX(10px) translateY(50%);
            // { rotation: 360, x: 10, yPercent: 50 }

            // анимация от указанного в объекте состояния к текущего состоянию
            gsap.from(".from", {y: 100, opacity: 0, duration: 3});

            // анимация от указанного состояния [fromVars] к указаному состоянию [toVars]
            gsap.fromTo(".fromTo", {y: 50, opacity: 0.75}, {y: 100, opacity: 0.25, duration: 3});

            // мгновенная установка свойств (без анимации)
            gsap.set(".set", {y: 100, opacity: 0.25, duration: 3});

        }, secondRef);
        return () => ctx.revert();
    }, []);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            gsap.to(".once", {y: 100, duration: 2});
            gsap.to(".infinitely", {y: 100, duration: 2, repeat: -1});
            gsap.to(".yoyo", {y: 100, duration: 2, repeat: -1, yoyo: true});
        }, thirdRef);
        return () => ctx.revert();
    }, []);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            const tween = gsap.to(".repeat", {
                    y: 100,
                    duration: 2,
                    repeat: 10,
                    onRepeat: () => {
                        setCount(count + 1)
                    },
                }
            );
            //console.log(tween)
        }, thirdRef);
        return () => ctx.revert();
    }, [
        //count
    ]);

    useEffect(() => {
        gsap
            .timeline({repeat: -1})
            .to(".boxAnimLeft", {x: -200, ease: "bounce.out", duration: 2})
            .to(".boxAnimRight", {x: 200, ease: "bounce.out", duration: 2})
            .to(".boxAnimLeft", {x: 0, duration: 0.5})
            .to(".boxAnimRight", {x: 0, duration: 0.5}, "<")
    }, []);

    useEffect(() => {
        clampValue();
        console.log("clampFunction(25): ", clampFunction(25));
        console.log("clampFunction(75): ", clampFunction(75));
        console.log("clampFunction(125): ", clampFunction(125));
        console.log("getPrefixName(filter): ", getPrefixName("filter"));
        console.log("getUnit('125px'): ", getUnit("125px"));
        console.log("getUnit('75%'): ", getUnit("75%"));
        console.log("getUnit('50vw'): ", getUnit("50vw"));
        console.log("interpolate(0, 100, 0.33): ", gsap.utils.interpolate(0, 100, 0.33));
        console.log("interpolate('100px', '200px', 0.25): ", gsap.utils.interpolate('100px', '200px', 0.25));
        console.log("interpolate('#000', '#FFF', 0.25): ", gsap.utils.interpolate('#000', '#FFF', 0.25));
        console.log("interpolate('1px solid #000', '10px solid #000', 0.25): ", gsap.utils.interpolate('1px solid #000', '10px solid #000', 0.25));
        console.log("interpolate({width: 10, color: '#333'}, {width: 20, color: '#666'}, 0.5): ", gsap.utils.interpolate({
            width: 10,
            color: '#333'
        }, {width: 20, color: '#666'}, 0.5));
        console.log("interpolate([10, 50, 80], 0.25): ", gsap.utils.interpolate([10, 50, 80], 0.25))
        console.log("interpolate(0.25): ", interpolate(0.25))
        console.log("interpolateArray(0.25): ", interpolateArray(0.25));
        console.log("interpolateArray(0.5): ", interpolateArray(0.5));
        console.log("interpolateArray(0.75): ", interpolateArray(0.75));
        console.log("mapRange(0, 100, 200, 800, 50): ", gsap.utils.mapRange(0, 100, 200, 800, 50));
        console.log("mapRange(50): ", mapRange(50));
        console.log("mapRange(25): ", mapRange(25));
        console.log("mapRange(75): ", mapRange(75));
        console.log("normalize(0, 10, 5): ", gsap.utils.normalize(0, 10, 5));
        console.log("normalize(5): ", normalize(5));
        console.log("normalize(2): ", normalize(2));
        console.log("normalize(8): ", normalize(8));
        console.log("gsap.utils.random(0, 10): ", gsap.utils.random(0, 10));
        console.log("gsap.utils.random(0, 10): ", gsap.utils.random(0, 10));
        console.log("gsap.utils.random(0, 10): ", gsap.utils.random(0, 10));
        console.log("gsap.utils.random(0, 10, 1): ", gsap.utils.random(0, 10, 1));
        console.log("gsap.utils.random(0, 10, 1): ", gsap.utils.random(0, 10, 1));
        console.log("gsap.utils.random(0, 10, 1): ", gsap.utils.random(0, 10, 1));
        console.log("gsap.utils.random([0, 1, 4, 7, 9]): ", gsap.utils.random([0, 1, 4, 7, 9]));
        console.log("gsap.utils.random([0, 1, 4, 7, 9]): ", gsap.utils.random([0, 1, 4, 7, 9]));
        console.log("gsap.utils.random([0, 1, 4, 7, 9]): ", gsap.utils.random([0, 1, 4, 7, 9]));
        console.log("randomFromArray(): ", randomFromArray());
        console.log("randomFromArray(): ", randomFromArray());
        console.log("randomFromArray(): ", randomFromArray());
        console.log("gsap.utils.shuffle([1, 2, 3, 4, 5]): ", gsap.utils.shuffle([1, 2, 3, 4, 5]));
        console.log("gsap.utils.snap(5, 19.5): ", gsap.utils.snap(5, 19.5));
        console.log("gsap.utils.snap(5, 15.1): ", gsap.utils.snap(5, 15.1));
        console.log("snap(1.8): ", snap(1.8));
        console.log("snap(2.1): ", snap(2.1));
        console.log("gsap.utils.snap([1, 3, 9], 2): ", gsap.utils.snap([1, 3, 9], 2));
        console.log("gsap.utils.snap([1, 3, 9], 7): ", gsap.utils.snap([1, 3, 9], 7));
        console.log("snapToArray(2): ", snapToArray(2));
        console.log("snapToArray(7): ", snapToArray(7));
        console.log("gsap.utils.snap({values: [10, 50], radius: 10}, 19): ", gsap.utils.snap({
            values: [10, 50],
            radius: 10
        }, 19));
        console.log("gsap.utils.snap({values: [10, 50], radius: 10}, 30): ", gsap.utils.snap({
            values: [10, 50],
            radius: 10
        }, 30));
        console.log("gsap.utils.snap({values: [10, 50], radius: 10}, 41): ", gsap.utils.snap({
            values: [10, 50],
            radius: 10
        }, 41));
        console.log(snapWithRadius(19));
        console.log(snapWithRadius(30));
        console.log(snapWithRadius(41));
        console.log('gsap.utils.splitColor("red"): ', gsap.utils.splitColor("red"));
        console.log('gsap.utils.splitColor("grb(45, 76, 89, 0.5)"): ', gsap.utils.splitColor("grb(45, 76, 89, 0.5)"));
        console.log('gsap.utils.splitColor("green", true): ', gsap.utils.splitColor("green", true));
        console.log("unitizeClamp(9): ", unitizeClamp(9));
        console.log("unitizeClamp(14): ", unitizeClamp(14));
        console.log("unitizeClamp(21): ", unitizeClamp(21));
        // @ts-ignore
        console.log("unitizeClamp('21%'): ", unitizeClamp('21%'));
        console.log("wrapRange(0):", wrapRange(0));
        console.log("wrapRange(5):", wrapRange(5));
        console.log("wrapArray(0): ", wrapArray(0));
        console.log("wrapArray(4): ", wrapArray(4));
        console.log("wrapYoyoRange(7): ", wrapYoyoRange(7));
        console.log("wrapYoyoArray(5): ", wrapYoyoArray(5));
    }, [])


    return (
        <div className={style.basic}>

            <div className={style.firstRow}>

                <div className={style.first} ref={appRef}>
                    <div className={clsx("blue", style.blue)}>Class selector</div>
                    <div className={style.box} id="box">Id selector</div>
                    <div ref={circleRef} className={style.circle}>Ref</div>
                    <p className={style.label}>App scope</p>
                </div>

                <div className={clsx("blue", style.outside_blue)}>Selector outside the app scope</div>

            </div>

            <div className={style.secondRow} ref={secondRef}>
                <div className={clsx(style.to, "to")}>to</div>
                <div className={clsx(style.from, "from")}>from</div>
                <div className={clsx(style.fromTo, "fromTo")}>fromTo</div>
                <div className={clsx(style.set, "set")}>set</div>
            </div>

            <div className={style.thirdRow} ref={thirdRef}>
                <div className={clsx(style.once, "once")}>once</div>
                <div className={clsx(style.repeat, "repeat")}>repeat: {count}/10</div>
                <div className={clsx(style.infinitely, "infinitely")}>infinitely</div>
                <div className={clsx(style.yoyo, "yoyo")}>yoyo</div>
            </div>

            <div className={style.fourRow}>
                <div className={clsx(style.boxAnimLeft, "boxAnimLeft")}/>
                <div className={clsx(style.boxAnimRight, "boxAnimRight")}/>
            </div>

        </div>

    )
}
