import React, {useLayoutEffect, useRef} from "react";
import style from "./ScrollPin.module.scss";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import clsx from "clsx";

gsap.registerPlugin(ScrollTrigger);

export const ScrollPin = () => {
    const appRef = useRef<HTMLDivElement>(null!);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            gsap.to(".box1", {
                    x: 500,
                    scrollTrigger: {
                        trigger: ".box1",
                        markers: true,
                        start: "top bottom-=300px",
                        end: "bottom 300px",
                        scrub: true,
                        pin: true,
                        pinSpacing: false,

                    }
                }
            )
        }, appRef);
        return () => ctx.revert();
    }, []);

    return (
        <div className={style.scrollPin} ref={appRef}>
            <div className={clsx(style.box1, "box1")}>Box 1</div>
            <div className={clsx(style.box2, "box2")}>Box 2</div>
        </div>
    )
}
