import React, {useLayoutEffect, useRef} from "react";
import style from "./FadeEnter.module.scss";
import gsap from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import clsx from "clsx";

gsap.registerPlugin(ScrollTrigger);

export const FadeEnter = () => {
    const length = 200;
    const appRef = useRef<HTMLDivElement>(null!);

    useLayoutEffect(() => {
        const ctx = gsap.context(() => {
            const boxes = gsap.utils.toArray<gsap.DOMTarget>(".box")

            boxes.forEach(box => {
                gsap.to(box,{
                        opacity: 1,
                        scrollTrigger: {
                            trigger: box,
                            markers: true,
                            start: "bottom bottom",
                            end: "bottom bottom-=100px",
                            scrub: true,
                        }
                    },
                )
            })

        }, appRef)
        return () => ctx.revert();
    }, [])

    return (
        <div className={style.fadeEnter}
             ref={appRef}
        >
            <div className={style.inner}>
                {
                    Array.from({length}, (_, k) => k)
                        .map(index => (
                            <div key={index}
                                 className={clsx(style.box, "box")}
                            />
                        ))
                }
            </div>

        </div>
    )
}
