import React, {useLayoutEffect, useRef} from "react";
import style from "./Wrap.module.scss";
import clsx from "clsx";
import gsap from "gsap";

export const Wrap = () => {
    const appRef = useRef<HTMLDivElement>(null!);

    useLayoutEffect(() => {
        const l = 1000000;
        const duration = l / 500;

        const ctx = gsap.context(() => {
            const ballWrap = gsap.utils.wrap(0, window.innerWidth - 200);
            const ballWrapYoyo = gsap.utils.wrapYoyo(0, window.innerWidth - 200);
            gsap.to(".ballWrap", {
                x: l,
                modifiers: {
                    x: (x, target) => {
                        const w = gsap.utils.unitize(ballWrap);
                        //console.log(target)
                        return w(x)
                    }
                },
                duration,
                ease: "none",
            });

            gsap.to(".ballWrapYoyo", {
                x: l,
                modifiers: {
                    x: x => {
                        const w = gsap.utils.unitize(ballWrapYoyo);
                        return w(x)
                    }
                },
                duration,
                ease: "none",
            });

        }, appRef);
        return () => ctx.revert();
    }, [])

    return (
        <div className={style.wrap}
             ref={appRef}
        >
            <div className={clsx(style.ballWrap, "ballWrap")}>ball Wrap</div>
            <div className={clsx(style.ballWrapYoyo, "ballWrapYoyo")}>ball Wrap Yoyo</div>
        </div>
    )
}
