import React, {useLayoutEffect, useRef, useState} from "react";
import style from "./SnapMapRangeInterpolate.module.scss"
import gsap from "gsap";

export const SnapMapRangeInterpolate = () => {

    // valueToSnap: 0...300 => 0
    // valueToSnap: 300...window.innerWidth - 300 => valueToSnap
    // valueToSnap: window.innerWidth - 300...window.innerWidth => window.innerWidth
    const snapEdges = gsap.utils.snap({values: [0, window.innerWidth], radius: 300});
    // преобразует число из диапазона [0, window.innerWidth] в диапазон [0, 1]
    const widthToProgress = gsap.utils.mapRange(0, window.innerWidth, 0, 1);
    // интеполяция между двумя цветами
    const interpColor = gsap.utils.interpolate("#C00", "#00C");
    // последовательное соединение функций
    const widthToColor = gsap.utils.pipe(
        snapEdges,
        widthToProgress,
        interpColor
    );

    const [color, setColor] = useState("#C00");

    const onMouseMove = (e: React.MouseEvent<HTMLDivElement>) => {
        setColor(widthToColor(e.clientX));
    }

    return (
        <div className={style.snapMapRangeInterpolate}
             onMouseMove={(e) => onMouseMove(e)}
             style={{
                 backgroundColor: color
             }}
        >

        </div>
    )
}
