import React, {useLayoutEffect, useRef} from "react";
import style from "./TimelineCircle.module.scss";
import clsx from "clsx";
import gsap from "gsap";
import {CustomEase} from "gsap/CustomEase";

gsap.registerPlugin(CustomEase);

export const TimelineCircle = () => {
    const appRef = useRef<HTMLDivElement>(null!);
    const count = 12;
    const duration = 0.5;
    const RADIUS = 250;
    const radius = 25;
    const arr = Array.from({length: count}, (_, k) => k);
    const rotation = (360 / count) - 360 * radius / ( Math.PI * RADIUS);

    CustomEase.create("custom", "M0,0,C0.222,0,0.415,0.112,0.5,0.212,0.6,0.33,0.736,0.682,0.8,1,0.812,0.964,0.814,0.922,0.826,0.892,0.844,0.845,0.87,0.832,0.904,0.868,0.933,0.898,0.954,0.906,1,1");
    const ease = "custom"; // "bounce.out";
    const yoyoEase = "custom";

    useLayoutEffect(() => {
        const ctx = gsap.context((self) => {
            const tl = gsap.timeline({ repeat: -1, yoyo: true });

            // @ts-ignore
            const items = self.selector(".itemWrapper") as TweenTarget[];

            items.forEach((item, index) => {
                tl.to(item, {
                    duration, ease, yoyoEase, rotation,
                }, index === 0 ? ">" : ">-0.1")
            })

        }, appRef);
        return () => ctx.revert();
    }, []);


    return (
        <div ref={appRef}
             className={style.timelineCircle}
        >
            {
                arr.map((key) => {
                    const y = RADIUS * Math.sin(2 * key * Math.PI / count);
                    const x = RADIUS * Math.cos(2 * key * Math.PI / count)

                    return (
                        <div key={key}
                             className={clsx("itemWrapper", style.itemWrapper)}
                        >
                            <div
                                className={style.item}
                                style={{
                                    top: `calc(-25px - ${y}px)`,
                                    left: `calc(-25px - ${x}px)`,
                                }}
                            />
                        </div>

                    )
                })
            }
        </div>
    )
}
